@font-face {
  font-family: "Roboto";
  src: local("Roboto"), local("Roboto-Regular"), url("../fonts/Robotoregular.woff2") format("woff2"), url("../fonts/Robotoregular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  src: local("Roboto Bold"), local("Roboto-Bold"), url("../fonts/Robotobold.woff2") format("woff2"), url("../fonts/Robotobold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  src: local("Roboto-Light"), local("Roboto Light"), url("../fonts/Robotolight.woff2") format("woff2"), url("../fonts/Robotolight.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Playfair Display";
  src: local("Playfair-Display"), local("Playfair Display"), url("../fonts/PlayfairDisplayregular.woff2") format("woff2"), url("../fonts/PlayfairDisplayregular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Playfair Display";
  src: local("PlayfairDisplay-Bold"), local("PlayfairDisplay Bold"), url("../fonts/PlayfairDisplaybold.woff2") format("woff2"), url("../fonts/PlayfairDisplaybold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Prata";
  src: local("Prata"), local("Prata-Regular"), url("../fonts/Prataregular.woff2") format("woff2"), url("../fonts/Prataregular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
*,
::after,
::before {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  height: 100%;
}

a {
  color: inherit;
  text-decoration: none;
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
  display: inline-block;
  vertical-align: top;
}

body {
  margin: 0;
  padding: 0;
  background-color: var(--default-white);
  font-family: var(--main-font);
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  font-style: normal;
  min-height: 100%;
  color: var(--main-black);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  appearance: none;
  margin: 0;
}

.visually-hidden {
  position: absolute;
  width: 0;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0, 0, 0, 0);
}

section {
  outline: 0.5px dotted #ffa200;
}

header {
  outline: 0.5px dotted red;
}

main {
  outline: 0.5px dotted green;
}

footer {
  outline: 0.5px dotted yellow;
}

:root {
  --default-black: #000000;
  --default-white: #ffffff;
  --arial: "Arial", "sans-serif", "Verdana";
  --tablet-width: 768px;
  --desktop-width: 1366px;
  --mobile-width: 320px;
  --main-font: "Roboto", "Arial", "sans-serif";
  --playfair-font: "Playfair Display", "Arial", "sans-serif";
  --prata-font: "Prata", "Arial", "sans-serif";
  --main-black: #555248;
  --white-background1: #FDFDFB;
  --grey1: #E6E6E6;
  --grey2: #8B8371;
  --grey3: #444240;
  --grey4: #C4C0B6;
  --yellow1: #E6C8B4;
  --black1: #1B1A17;
  --black2: #444240;
  --mobile-padding: 20px;
  --tablet-padding: 70px;
  --desktop-r-padding: 98px;
  --desktop-l-padding: 94px;
}

.main-header {
  background-color: var(--white-background1);
  line-height: 16px;
}

.main-header__wrapper {
  width: 100%;
  margin: 0 auto;
  padding-left: var(--mobile-padding);
  padding-right: var(--mobile-padding);
  padding-left: 0;
  padding-right: 0;
  padding-top: 20px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .main-header__wrapper {
    padding-left: var(--tablet-padding);
    padding-right: var(--tablet-padding);
  }
}
@media (min-width: 1366px) {
  .main-header__wrapper {
    max-width: var(--desktop-width);
    padding-left: var(--desktop-l-padding);
    padding-right: var(--desktop-r-padding);
  }
}
@media (min-width: 768px) {
  .main-header__wrapper {
    padding-top: 34px;
    flex-direction: row;
    align-items: flex-start;
    justify-content: flex-start;
    min-height: 85px;
  }
}
.main-header__logo-link {
  font-family: var(--playfair-font);
  font-size: 12px;
  text-transform: uppercase;
  letter-spacing: 0.14em;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  width: 107px;
  min-height: 26px;
  margin-bottom: 10px;
}
@media (min-width: 768px) {
  .main-header__logo-link {
    margin-top: 1px;
  }
}
@media (min-width: 1366px) {
  .main-header__logo-link {
    margin-right: 266px;
  }
}

.main-header__logo-wrapper {
  align-self: center;
  width: 19.2px;
  height: 19.2px;
}

.main-header__logo-text {
  margin: 0;
  margin-top: 2px;
  margin-left: 3px;
}

.main-header__nav {
  width: 100%;
}
@media (min-width: 768px) {
  .main-header__nav {
    width: 50%;
    margin: 0 auto;
  }
}
@media (min-width: 1366px) {
  .main-header__nav {
    width: auto;
    margin: 0;
    margin-right: auto;
  }
}

.main-header__nav-list {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
}
@media (min-width: 768px) {
  .main-header__nav-list {
    flex-direction: row;
    justify-content: space-around;
  }
}

@media (min-width: 1366px) {
  .main-header__nav-item:not(:last-child) {
    margin-right: 65px;
  }
}

.main-header__nav-link {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 5px 0;
  position: relative;
}
.main-header__nav-link::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 1px;
  background-color: var(--grey1);
  top: -1px;
  left: 0;
}
.main-header__nav-link::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 1px;
  background-color: var(--grey1);
  bottom: 0;
  left: 0;
}
@media (min-width: 768px) {
  .main-header__nav-link {
    padding: 0;
  }
  .main-header__nav-link::before, .main-header__nav-link::after {
    content: none;
  }
}

.main-header__utils {
  width: 100%;
}
@media (min-width: 768px) {
  .main-header__utils {
    width: auto;
  }
}

.main-header__utils-list {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-around;
}
@media (min-width: 768px) {
  .main-header__utils-list {
    justify-content: space-between;
    min-width: 99px;
  }
}

.main-header__utils-item {
  width: 100%;
}
@media (min-width: 768px) {
  .main-header__utils-item {
    width: 25%;
  }
}

.main-header__utils-link {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 100%;
  padding: 10px 0;
}
@media (min-width: 768px) {
  .main-header__utils-link {
    padding: 0;
  }
}

.main-header__utils-link-icon {
  position: relative;
}

.main-header__utils-icon {
  position: relative;
}

.main-header__utils-round {
  position: absolute;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  width: 13px;
  height: 13px;
  background: var(--yellow1);
  border-radius: 50%;
  top: -5px;
  right: -5px;
  font-size: 8px;
  color: var(--black1);
}

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  font-size: 16px;
  line-height: 20px;
}
@media (min-width: 768px) {
  .main-nav__list {
    font-size: 14px;
    line-height: 13px;
  }
}

.main-utils__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.main-container {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  flex-grow: 1;
}

.preview {
  background-color: var(--white-background1);
}

.preview__wrapper {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  width: 100%;
  margin: 0 auto;
  padding-left: var(--mobile-padding);
  padding-right: var(--mobile-padding);
  padding-left: 0;
  padding-right: 0;
}
@media (min-width: 768px) {
  .preview__wrapper {
    padding-left: var(--tablet-padding);
    padding-right: var(--tablet-padding);
  }
}
@media (min-width: 1366px) {
  .preview__wrapper {
    max-width: var(--desktop-width);
    padding-left: var(--desktop-l-padding);
    padding-right: var(--desktop-r-padding);
  }
}
@media (min-width: 768px) {
  .preview__wrapper {
    flex-direction: row;
    min-height: 768px;
    padding-left: var(--tablet-padding);
    padding-right: 0;
    padding-top: 0;
    justify-content: space-between;
  }
}
@media (min-width: 1366px) {
  .preview__wrapper {
    padding-left: 185px;
    justify-content: flex-start;
    background-image: url("../img/mobile/preview-background.webp");
    background-size: 496px 944px;
    background-repeat: no-repeat;
    background-position-y: center;
    background-position-x: 480px;
  }
}

.preview__description {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
  padding-top: 34px;
}
@media (min-width: 768px) {
  .preview__description {
    margin-top: 200px;
    padding-top: 0;
  }
}
@media (min-width: 1366px) {
  .preview__description {
    align-items: flex-start;
    margin-top: 236px;
  }
}

.preview__title {
  font-family: var(--playfair-font);
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  font-weight: 400;
  color: var(--black1);
  font-size: 24px;
  line-height: 1;
  margin: 0;
  max-width: 75%;
  margin-bottom: 20px;
}
@media (min-width: 768px) {
  .preview__title {
    position: relative;
    margin-bottom: 16px;
    max-width: none;
  }
  .preview__title::after {
    content: "";
    position: absolute;
    width: 50px;
    height: 1px;
    top: 50%;
    bottom: auto;
    left: -70px;
    right: auto;
    background-color: var(--grey2);
    background-size: auto;
  }
}
@media (min-width: 1366px) {
  .preview__title {
    font-size: 45px;
    line-height: 60px;
    font-weight: 700;
  }
  .preview__title::after {
    left: -65px;
  }
}

.preview__subtitle {
  font-family: var(--playfair-font);
  font-weight: 400;
  margin: 0;
  max-width: 75%;
  margin-bottom: 20px;
}
@media (min-width: 768px) {
  .preview__subtitle {
    margin-bottom: 29px;
  }
}
@media (min-width: 1366px) {
  .preview__subtitle {
    font-size: 30px;
    line-height: 31px;
  }
}

.preview__description-slogan {
  font-size: 12px;
  display: inline;
  text-align: center;
  word-wrap: break-word;
  line-height: 1;
  font-weight: 300;
  margin: 0;
  max-width: 75%;
  margin-bottom: 30px;
}
@media (min-width: 768px) {
  .preview__description-slogan {
    margin-bottom: 64px;
  }
}
@media (min-width: 1366px) {
  .preview__description-slogan {
    font-size: 14px;
    line-height: 21px;
    text-align: start;
    width: 260px;
  }
}

.preview__link {
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.05em;
  padding: 14px 18px;
  width: 100%;
  text-align: center;
  background-color: var(--black2);
  color: var(--default-white);
  margin-top: auto;
}
@media (min-width: 768px) {
  .preview__link {
    margin-top: 0;
    width: fit-content;
    padding: 26px 38px;
  }
}

.preview__card {
  display: flex;
  position: relative;
  flex-direction: column;
  flex-wrap: wrap;
  min-height: 250px;
  justify-content: space-between;
  padding-top: 20px;
  padding-bottom: 20px;
  font-family: var(--prata-font);
  color: var(--black1);
  font-size: 16px;
  line-height: 16px;
}
.preview__card::before {
  content: "";
  position: absolute;
  background-image: url("../img/mobile/preview-image.webp");
  background-repeat: no-repeat;
  background-position-y: center;
  background-position-x: right;
  width: 50%;
  max-width: 515px;
  top: 0;
  bottom: 0;
  right: 0;
  left: auto;
  background-size: auto 100%;
}
@media (min-width: 768px) {
  .preview__card {
    padding: 0;
    flex-grow: 1;
    justify-content: space-around;
  }
  .preview__card::before {
    width: 75%;
    background-size: 100% auto;
  }
}
@media (min-width: 1366px) {
  .preview__card {
    justify-content: flex-start;
    align-items: flex-end;
  }
  .preview__card::before {
    max-width: none;
    width: 500px;
    background-size: 502px 100%;
    background-position-x: center;
    right: 198px;
    bottom: -30px;
  }
}

.preview__card-price {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  margin-left: 20px;
}
@media (min-width: 768px) {
  .preview__card-price {
    margin-left: 0;
  }
}
@media (min-width: 1366px) {
  .preview__card-price {
    margin-top: 138px;
    margin-right: 710px;
    margin-bottom: auto;
  }
}

.preview__card-number {
  margin-right: 7px;
  height: 16px;
}

.preview__card-price-wrapper {
  position: relative;
}
.preview__card-price-wrapper::after {
  content: "";
  position: absolute;
  width: 50px;
  height: 1px;
  top: 50%;
  bottom: auto;
  right: -100px;
  left: auto;
  background-color: var(--grey2);
  background-size: auto;
}
@media (min-width: 768px) {
  .preview__card-price-wrapper::after {
    width: 30px;
    right: -40px;
  }
}
@media (min-width: 1366px) {
  .preview__card-price-wrapper::after {
    width: 50px;
    right: -65px;
  }
}

.preview__card-price-svg {
  fill: none;
}

.preview__card-parameters {
  margin: 0;
  margin-left: 20px;
}
@media (min-width: 768px) {
  .preview__card-parameters {
    margin-left: 0;
  }
}
@media (min-width: 1366px) {
  .preview__card-parameters {
    margin-bottom: 116px;
    margin-right: 114px;
  }
}

.preview__card-parameter {
  position: relative;
}
.preview__card-parameter::after {
  content: "";
  position: absolute;
  width: 50px;
  height: 1px;
  top: 50%;
  bottom: auto;
  right: -70px;
  left: auto;
  background-color: var(--grey2);
  background-size: auto;
}
@media (min-width: 768px) {
  .preview__card-parameter::after {
    width: 30px;
    right: -40px;
  }
}
@media (min-width: 1366px) {
  .preview__card-parameter::after {
    width: 50px;
    right: auto;
    left: -65px;
  }
}

.popular {
